import React, { useContext } from 'react';
import { Context, PriceContext } from '../../Context';
import './BasketItem.css';

const BasketItem = ({ item }) => {
	const [items, setItems] = useContext(Context);
	const [price, setPrice] = useContext(PriceContext);

	const deleteHandler = () => {
		const newItems = [...items];
		let newPrice = price;

		for (const value of newItems)
			if (value.id === item.id) {
				value.inBasket = false;
				newPrice -= value.price;
				break;
			}

		setItems(newItems);
		setPrice(newPrice);
	};

	return (
		<div className="basket_item">
			<div className="basket_item-img">
				<img src={item.imgPath} alt="img" />
			</div>
			<div className="basket_item-info">
				<div className="basket_item-title">{item.name}</div>
				<div className="basket_item-price">{item.price} руб.</div>
			</div>
			<div className="basket_item-delete" onClick={deleteHandler}>
				<img src="./img/delete.svg" alt="X" />
			</div>
		</div>
	);
};

export default BasketItem;
